export default "<template>\r\n  <div class=\"MonHocToan\">\r\n    <raw-loader :mytxt=\" require('!raw-loader!./UseSelect23.vue').default \"></raw-loader> \r\n    <h1>This is an Toan page</h1>\r\n    <div>\r\n      <!-- <select class=\"js-example-basic-single\" name=\"state\">\r\n        <option value=\"AL\">Alabama</option>\r\n        <option value=\"WY\">Wyoming</option>\r\n      </select> -->\r\n    </div>\r\n    \r\n    <b-modal ref=\"my-modal2\" id=\"myModal\" size=\"sm\" :no-close-on-backdrop=\"true\" hide-footer title=\"BootstrapVue\">\r\n      <form @submit.stop.prevent=\"onSaveModal\">\r\n        <div>\r\n\r\n          <!-- <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">Id</label>\r\n            <div class=\"col-sm-8\">\r\n              <input type=\"text\" class=\"form-control\" v-model=\"r.Id\" />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Name\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n              <input \r\n                type=\"text\" \r\n                v-model=\"r.Name\"\r\n                v-validate=\"'required'\"\r\n                name=\"Name\"\r\n                :class=\" 'form-control' \" \r\n              />\r\n            </div>\r\n          </div> \r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Thành phố\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n\r\n              <v-select\r\n                v-model=\"r.Tp\"\r\n                v-validate=\"'required'\"\r\n                :reduce=\"(option) => option.id\"\r\n                :options=\"[\r\n                  { label: 'Huế', id: 'HUE' },\r\n                  { label: 'Sài gòn', id: 'SG' },\r\n                  { label: 'Đà nẵng', id: 'DN' },\r\n                ]\"\r\n              />\r\n\r\n            </div>\r\n          </div>\r\n          -->\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Thành phố\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n\r\n              <select class=\"js-example-basic-single\" name=\"state\">\r\n                <option value=\"AL\">Alabama</option>\r\n                <option value=\"WY\">Wyoming</option>\r\n              </select>\r\n\r\n\r\n            </div>\r\n          </div>\r\n\r\n\r\n\r\n\r\n        </div>  \r\n        <div class=\"float-right\">\r\n          <button type=\"submit\" class=\"btn btn-primary mr-1 btn-sm\">Save</button>\r\n          <button type=\"button\" class=\"btn btn-sm\" @click=\"hideModal()\">Cancel</button>\r\n          <button id=\"test\">test</button>\r\n        </div>\r\n      </form>\r\n    </b-modal>\r\n\r\n\r\n    <button class=\"btn btn-primary btn-sm m-1\" @click=\"onAdd\">Add</button>\r\n\r\n\r\n\r\n\r\n\r\n  </div>\r\n</template>\r\n<script>\r\n\r\nvar MyLoadSelect2 = {\r\n  test() {\r\n    return 'hello test';\r\n  },\r\n  loadCss(url) {\r\n    return new Promise((resolve, reject) => {\r\n      let link = document.createElement('link');\r\n      link.type = 'text/css';\r\n      link.rel = 'stylesheet';\r\n      link.onload = resolve;\r\n      link.href = url;\r\n      let headScript = document.querySelector('script');\r\n      headScript.parentNode.insertBefore(link, headScript);\r\n    });\r\n  },\r\n  loadScript(id, url) {\r\n    return new Promise(function (resolve, reject) {\r\n      var script = document.createElement(\"script\");\r\n      script.onload = resolve;\r\n      script.onerror = reject;\r\n      script.src = url;\r\n      script.type = \"text/javascript\";\r\n      script.setAttribute('id', id);\r\n      document.getElementsByTagName(\"head\")[0].appendChild(script);\r\n    });\r\n  },\r\n}\r\n\r\n\r\nexport default {\r\n  name: 'Tpl',\r\n  components: {\r\n  },\r\n\r\n  data(){\r\n    return{\r\n      r:{\r\n        Id:1,\r\n        Name:\"thuc 101\",\r\n        Tuoi:100,\r\n        Tp:'DN'\r\n      },\r\n    }\r\n  },\r\n  mounted(){\r\n    if (!document.getElementById(\"my-load2-select2\")) {\r\n        MyLoadSelect2.loadCss(\"https://unpkg.com/select2@4.0.3/dist/css/select2.min.css\").then(() => {\r\n          MyLoadSelect2.loadScript(\"my-load2-select2\", \"https://unpkg.com/jquery@3.1.1/dist/jquery.js\").then(() => {\r\n            MyLoadSelect2.loadScript(\"\", \"https://unpkg.com/select2@4.0.3/dist/js/select2.js\").then(() => {\r\n              this.onReady();\r\n            });\r\n          })\r\n        })\r\n      }else{\r\n        this.onReady();\r\n      }\r\n  },\r\n  methods:{\r\n    onReady(){\r\n      let that=this;\r\n      $(document).ready(function() {\r\n\r\n\r\n        // $('#mySelect2').select2({\r\n        //   dropdownParent: $('#myModal')\r\n        // });\r\n        that.$nextTick(function(){\r\n        })\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n      });      \r\n    },\r\n\r\n    onSaveModal(){\r\n      alert('hi');\r\n    },    \r\n\r\n\r\n\r\n    onAdd(){\r\n      this.showModal();\r\n    },\r\n    toast(msg, variant = null) {\r\n      this.$bvToast.toast(msg, {\r\n        title: `Information`,\r\n        variant: variant,\r\n        solid: true,\r\n      });\r\n    },\r\n    showModal() {\r\n        this.$refs['my-modal2'].show();\r\n\r\n        $('.js-example-basic-single').select2({\r\n            dropdownParent: $('#myModal')\r\n        });\r\n\r\n\r\n\r\n\r\n\r\n    },\r\n    hideModal() {\r\n      this.$refs['my-modal2'].hide()\r\n    },\r\n\r\n\r\n\r\n\r\n  }\r\n}\r\n</script>\r\n";